import React from 'react';
import A from '../../helpers/A';
import Form from '../../helpers/Form';

// import '../../styles/static/index.css';

import logo_poli from '../../images/logo_poli.png';


function Index() {
    
    return (
        <React.Fragment>
            <link rel="stylesheet" href="../../style/interpoli/static/index.css" />

            {/* Main hero unit for a primary marketing message or call to action */}
            <div className="hero-unit" id="teste1">
                <img id="logoInter" className="span3" src={logo_poli} />
        
                <div className="span7">
                    <h1>Bem Vindo!</h1>
                    <p>O Banco de Dados InterPoli busca a criação de uma rede de contatos entre os alunos da POLI/UFRJ 
                        com percurso no exterior, além de funcionar também como uma ferramenta de apoio aos futuros 
                        intercambistas e à promoção dos programas de intercâmbio junto às empresas.</p>
                </div>
            </div>
        
            {/* Example row of columns */}
            <div className="row boxes">
                <div className="box1 span5">
                    <h2>Cadastre-se</h2>
                    <p>Você é aluno da Escola politécnica da UFRJ e pretende fazer ou já fez intercâmbio? 
                        Cadastre-se já através do link abaixo! </p>
                    <p><A className="btn" c="usuarios" a="cadastro">Cadastro &raquo;</A></p>
                </div>
                <div className="box2 span5">
                    <h2>Encontre pessoas</h2>
                    <p>Acesse nossa busca avançada e encontre alguém que tenha experiências de seu interesse.</p>
                    <p><A className="btn" c="usuarios" a="busca">Busca &raquo;</A></p>
                </div>
            </div>
        </React.Fragment>
    );
}

export default Index;